<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-自定义字体图标1" class="vue-doc-element">自定义字体图标</h2>
<p class="vue-doc-element">支持使用第三方自定义图标</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-iconshapes1" class="vue-doc-element">icon&amp;shapes</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          
        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        
          <div class="vue-doc-demo">
            <template>
    <div>
        <Icon prefix="pc-icon" type="yiyuanshuju" size="24" />
        <Icon prefix="pc-icon" type="yilianti" size="24" />
        <Icon prefix="pc-icon" type="xietang" size="24" />
    </div>
</template>
          </div>
            <div class="vue-doc-code" :class="{'vue-doc-code-show':showVueDocCode}">
              <div class="vue-doc-code-copy" @click="vueDocCodeCopy">{{vueDocCodeCopyText}}</div>
              <div class="vue-doc-code-content" ref="vueDocCodeBox">
                <pre class="vue-doc-element"><code class="lang-vue"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">prefix</span>=<span class="hljs-string">"pc-icon"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"yiyuanshuju"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"24"</span> /&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">prefix</span>=<span class="hljs-string">"pc-icon"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"yilianti"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"24"</span> /&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">prefix</span>=<span class="hljs-string">"pc-icon"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"xietang"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"24"</span> /&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
    @<span class="hljs-keyword">font-face</span> {
        <span class="hljs-attribute">font-family</span>: <span class="hljs-string">"custom-font"</span>;
        <span class="hljs-attribute">src</span>: <span class="hljs-built_in">url</span>(<span class="hljs-string">'data:application/x-font-woff;charset=utf-8;base64,'</span>) <span class="hljs-built_in">format</span>(<span class="hljs-string">'woff'</span>);
    }



<span class="hljs-selector-class">.pc-icon</span> {
  <span class="hljs-attribute">font-family</span>:<span class="hljs-string">"custom-font"</span> <span class="hljs-meta">!important</span>;
  <span class="hljs-attribute">font-size</span>:<span class="hljs-number">16px</span>;
  <span class="hljs-attribute">font-style</span>:normal;
  <span class="hljs-attribute">-webkit-font-smoothing</span>: antialiased;
  <span class="hljs-attribute">-moz-osx-font-smoothing</span>: grayscale;
}

<span class="hljs-selector-class">.pc-icon-shuju</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e600"</span>; }

<span class="hljs-selector-class">.pc-icon-users</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e626"</span>; }

<span class="hljs-selector-class">.pc-icon-yisheng</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e6ab"</span>; }

<span class="hljs-selector-class">.pc-icon-yitiji</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e604"</span>; }

<span class="hljs-selector-class">.pc-icon-yiyuanshuju</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e674"</span>; }

<span class="hljs-selector-class">.pc-icon-hushi</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e661"</span>; }

<span class="hljs-selector-class">.pc-icon-zhikong</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e668"</span>; }

<span class="hljs-selector-class">.pc-icon-yiyuan</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e62c"</span>; }

<span class="hljs-selector-class">.pc-icon-gaikuang</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e69a"</span>; }

<span class="hljs-selector-class">.pc-icon-gongneng</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e62f"</span>; }

<span class="hljs-selector-class">.pc-icon-zhanghao</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e60b"</span>; }

<span class="hljs-selector-class">.pc-icon-yonghushuxing</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e719"</span>; }

<span class="hljs-selector-class">.pc-icon-yilianti</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e624"</span>; }

<span class="hljs-selector-class">.pc-icon-quanxian</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e83a"</span>; }

<span class="hljs-selector-class">.pc-icon-xietang</span><span class="hljs-selector-pseudo">:before</span> { <span class="hljs-attribute">content</span>: <span class="hljs-string">"\e62b"</span>; }

</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>

              </div>
              <div class="vue-doc-code-toggle" @click="vueDocCodeToggle">
                <span class="vue-doc-code-toggle-text"></>{{showVueDocCode?"隐藏代码":"显示代码"}}</span>
                <span class="vue-doc-code-toggle-text-EN"></>{{showVueDocCode?"Hide Code":"Show Code"}}</span>
              </div>
            </div>
          


    </div>
  </template>
  <script>
      
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  <style>
    @font-face {
        font-family: "custom-font";
        src: url('data:application/x-font-woff;charset=utf-8;base64,') format('woff');
    }



.pc-icon {
  font-family:"custom-font" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.pc-icon-shuju:before { content: "\e600"; }

.pc-icon-users:before { content: "\e626"; }

.pc-icon-yisheng:before { content: "\e6ab"; }

.pc-icon-yitiji:before { content: "\e604"; }

.pc-icon-yiyuanshuju:before { content: "\e674"; }

.pc-icon-hushi:before { content: "\e661"; }

.pc-icon-zhikong:before { content: "\e668"; }

.pc-icon-yiyuan:before { content: "\e62c"; }

.pc-icon-gaikuang:before { content: "\e69a"; }

.pc-icon-gongneng:before { content: "\e62f"; }

.pc-icon-zhanghao:before { content: "\e60b"; }

.pc-icon-yonghushuxing:before { content: "\e719"; }

.pc-icon-yilianti:before { content: "\e624"; }

.pc-icon-quanxian:before { content: "\e83a"; }

.pc-icon-xietang:before { content: "\e62b"; }

</style>
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


